<template>
  <div class="container">
    <!-- 标题 -->
    <h2 class="cal-title">日程活动</h2>
    <!-- 月份增减 -->
    <div class="cal-head">
      <el-button type="text" @click="changeTime('reduce')"><i class="arrow-icon el-icon-arrow-left"></i></el-button>
      <span class="cal-head-date">{{dateData.curyear + '年' + dateData.curmonth + '月'}}</span>
      <el-button type="text" @click="changeTime('add')"><i class="arrow-icon el-icon-arrow-right"></i></el-button>
    </div>
    <!-- 日历表体 -->
    <div class="cal-body">
      <div class="cal-date-title">
        <span v-for="(val, i) in dateTitle" :key="i">{{val}}</span>
      </div>
      <div class="cal-day">
        <div v-for="(val, i) in dayArr" :key="i" class="day" @click="selectDate(val)"><span :class="{today: dateData.year === val.year && dateData.month === val.month && dateData.date === val.date, isCurMonth: dateData.curmonth === val.month}">{{val.date}}</span><i class="hasEvent" v-if="val.hasEvent"></i><i class="selectedday" v-if="dateData.curyear === val.year && dateData.curmonth === val.month && dateData.curdate === val.date"></i></div>
      </div>
    </div>
    <!-- 事件列表 -->
    <div class="cal-event-container">
      <div>
        <h3>
          <el-button class="add-event" icon="el-icon-plus" circle size="mini" type="warning" @click="handleEvent(null)"></el-button>
          {{dateData.curyear + ' / ' + dateData.curmonth + ' / ' + dateData.curdate}}
        </h3>
      </div>
      <ul class="cal-event">
        <li class="cal-event-item" :class="{notlast: filterEventList.length !== i+1}" v-for="(val, i) in filterEventList" :key="i">
          <el-popover placement="top" width="400" trigger="hover">
            <span slot="reference" @click="handleEvent(val)">{{++i + ' ' + val.title}}</span>
            <p><span style="font-weight: bold;">起止时间：</span>{{val.start}} 至 {{val.end}}</p>
            <p><span style="font-weight: bold;">事件：</span>{{val.title}}</p>
            <p><span style="font-weight: bold;">详情：</span>{{val.desc}}</p>
          </el-popover>
          <span class="el-icon-error del-btn" v-on:click="delEvent(val)"></span>
        </li>
        <li class="no-event" v-if="!filterEventList.length">当日无待办事项</li>
      </ul>
    </div>
    <!-- 添加事件对话框 -->
    <div class="add-dialog-wrap" v-if="addVisible">
      <div class="add-dialog-content">
        <p style="text-align: center;font-weight: bold;">{{dialogTitle}}</p>
        <el-form ref="form" label-width="80px" :model="addForm" :rules="addRules">
          <el-form-item label="事项名称" required prop="title">
            <el-input v-model="addForm.title" placeholder="请输入事件标题"></el-input>
          </el-form-item>
          <el-form-item label="事件详情" required prop="desc">
            <el-input v-model="addForm.desc" type="textarea" placeholder="请输入事件详情" maxlength="50" :rows="3"></el-input>
          </el-form-item>
          <el-form-item label="开始时间" required>
            <el-col>
              <el-date-picker type="date" v-model="addForm.start" placeholder="开始日期" style="width: 100%;"></el-date-picker>
            </el-col>
          </el-form-item>
          <el-form-item label="结束时间" required>
            <!-- <el-col class="line" :span="2">&nbsp;&nbsp;-</el-col> -->
            <el-col>
              <el-date-picker type="date" v-model="addForm.end" placeholder="结束日期" style="width: 100%;"></el-date-picker>
            </el-col>
          </el-form-item>
          <!-- <el-form-item label="活动性质">
            <el-checkbox-group v-model="sizeForm.type">
              <el-checkbox-button label="美食/餐厅线上活动" name="type"></el-checkbox-button>
              <el-checkbox-button label="地推活动" name="type"></el-checkbox-button>
              <el-checkbox-button label="线下主题活动" name="type"></el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="特殊资源">
            <el-radio-group v-model="sizeForm.resource" size="medium">
              <el-radio border label="线上品牌商赞助"></el-radio>
              <el-radio border label="线下场地免费"></el-radio>
            </el-radio-group>
          </el-form-item> -->
          <el-form-item>
            <el-button type="primary" @click.native="submitEvent">提交</el-button>
            <el-button @click.native="cancelEvent">取消</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'eventCalendar',
  data () {
    return {
      dialogTitle: '',
      dateData: {
        year: 1970, // 当前实际日期
        month: 1,
        day: 1,
        date: 1,
        curyear: 1970, // 页面显示日期
        curmonth: 1,
        curday: 1,
        curdate: 1
      },
      dateTitle: ['日', '一', '二', '三', '四', '五', '六'],
      dayArr: [], // 日期列表
      tempEvents: [],
      addForm: { // 新增事件信息
        title: '',
        desc: '',
        start: '',
        end: ''
      },
      editForm: {},
      addRules: {
        title: [
          {required: true, message: '请输入活动名称', trigger: 'blur'}
        ],
        desc: [
          {required: true, message: '请输入事件详情', trigger: 'blur'}
        ]
      },
      addVisible: false // 新增弹窗是否显示
    }
  },
  computed: {
    ...mapState({
      eventList: state => state.eventList
    }),
    // 字符化当天日期
    dayStr () {
      return this.formatDate(this.dateData.year, this.dateData.month, this.dateData.date)
    },
    // 字符化当前日期
    curDayStr () {
      return this.formatDate(this.dateData.curyear, this.dateData.curmonth, this.dateData.curdate)
    },
    // 筛选当前日期事件
    filterEventList () {
      return this.eventList.filter((val) => {
        let start = new Date(val.start)
        let end = new Date(val.end)
        let day = new Date(this.curDayStr)
        return day >= start && day <= end
      })
    }
  },
  methods: {
    // 初始化当前日期表
    initCalendar (initdate) {
      var date
      if (initdate) {
        date = new Date(initdate)
      } else {
        date = new Date()
      }
      let traceDay = 42 - (date.getDay() + 1)
      // 每月1号以前的日期
      let startday = this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate())
      let dayArr = [] // 日期数据缓存
      for (let i = date.getDay(); i > 0; i--) {
        let d = new Date(startday)
        d.setDate(d.getDate() - i)
        // 格式化日期
        let dStr = this.formatDate(d.getFullYear(), d.getMonth() + 1, d.getDate())
        // 判断日期是否有事项
        let hasEvent = this.eventList.some(function (item) {
          let start = new Date(item.start)
          let end = new Date(item.end)
          let day = new Date(dStr)
          return day >= start && day <= end
        })
        dayArr.push({
          date: d.getDate(),
          month: d.getMonth() + 1,
          year: d.getFullYear(),
          hasEvent: hasEvent
        })
      }
      // 当前视图的剩余日期
      for (let i = 0; i <= traceDay; i++) {
        let d = new Date(startday)
        d.setDate(d.getDate() + i)
        // 格式化日期
        let dStr = this.formatDate(d.getFullYear(), d.getMonth() + 1, d.getDate())
        // 判断日期是否有事项
        let hasEvent = this.eventList.some(function (item) {
          let start = new Date(item.start)
          let end = new Date(item.end)
          let day = new Date(dStr)
          return day >= start && day <= end
        })
        dayArr.push({
          date: d.getDate(),
          month: d.getMonth() + 1,
          year: d.getFullYear(),
          hasEvent: hasEvent
        })
      }
      this.dayArr = dayArr.slice(0)
    },
    // 格式化日期，传入年月日，例：2019-01-01
    formatDate (year, month, day) {
      var y = year
      var m = month
      if (m < 10) m = '0' + m
      var d = day
      if (d < 10) d = '0' + d
      return y + '-' + m + '-' + d
    },
    // 格式化日期，传入日期对象，例：2019-01-01
    formatDate1 (date) {
      let day = new Date(date)
      var y = day.getFullYear()
      var m = day.getMonth() + 1
      if (m < 10) m = '0' + m
      var d = day.getDate()
      if (d < 10) d = '0' + d
      return y + '-' + m + '-' + d
    },
    // 更改月份
    changeTime (op) {
      if (op === 'add') {
        if (this.dateData.curmonth === 12) {
          this.dateData.curmonth = 1
          this.dateData.curyear += 1
        } else {
          this.dateData.curmonth += 1
        }
      } else if (op === 'reduce') {
        if (this.dateData.curmonth === 1) {
          this.dateData.curmonth = 12
          this.dateData.curyear -= 1
        } else {
          this.dateData.curmonth -= 1
        }
      }
      this.initCalendar(this.formatDate(this.dateData.curyear, this.dateData.curmonth, 1))
    },
    // 选择日期
    selectDate (val) {
      // 如果点击日期不是当月，先构建目标月份视图
      if (this.dateData.curmonth !== val.month) {
        this.initCalendar(this.formatDate(val.year, val.month, 1))
      }
      this.dateData.curdate = val.date
      this.dateData.curmonth = val.month
      this.dateData.curyear = val.year
      // 设置新建事项的默认日期
      this.addForm.start = this.addForm.end = new Date(this.curDayStr)
    },
    // 事件对话框提交
    submitEvent () {
      let event = {
        start: this.formatDate1(this.addForm.start),
        end: this.formatDate1(this.addForm.end),
        title: this.addForm.title,
        desc: this.addForm.desc
      }
      if (this.editForm.title) {
        let i = this.tempEvents.indexOf(this.editForm)
        console.log(i)
        this.tempEvents.splice(i, 1)
        this.editForm = {}
      }
      if (this.addForm.title && this.addForm.title.length) {
        this.tempEvents.push(event)
        this.$store.commit('updateEvents', this.tempEvents)
        this.addVisible = false
      } else {
        this.$message('请填写完整事项信息！')
      }
    },
    // 事件对话框取消
    cancelEvent () {
      this.editForm = {}
      this.addVisible = false
    },
    // 新增、编辑事件
    handleEvent (item) {
      this.tempEvents = this.eventList
      this.addVisible = true
      if (item !== null) {
        this.dialogTitle = '编辑事项'
        this.editForm = item
        this.addForm = Object.assign({}, item)
        this.addForm.start = new Date(this.addForm.start)
        this.addForm.end = new Date(this.addForm.end)
      } else {
        this.dialogTitle = '新增事项'
        this.addForm.title = ''
        this.addForm.desc = ''
        this.addForm.start = new Date(this.curDayStr)
        this.addForm.end = new Date(this.curDayStr)
      }
    },
    // 删除事件
    delEvent (item) {
      this.tempEvents = this.eventList
      let i = this.tempEvents.indexOf(item)
      console.log(i)
      this.$confirm('<p>您确认删除该事项吗？<p><p>起止时间：' + item.start + ' - ' + item.end + '</p><p>事件：' + item.title + '</p>', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true
      }).then(() => {
        this.tempEvents.splice(i, 1)
        this.$store.commit('updateEvents', this.tempEvents)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  },
  mounted () {
    // 将时间设置为当天，当前选择日期也为当天，初始化日期视图
    let date = new Date()
    this.dateData.curdate = this.dateData.date = date.getDate()
    this.dateData.curyear = this.dateData.year = date.getFullYear()
    this.dateData.curmonth = this.dateData.month = date.getMonth() + 1
    this.dateData.curday = this.dateData.day = date.getDay()
    this.initCalendar(this.formatDate(this.dateData.year, this.dateData.month, 1))
    // 设置新建事项的默认日期
    this.addForm.start = this.addForm.end = new Date(this.dayStr)
  },
  watch: {
    // 监视当前日期的事件变化
    filterEventList: function () {
      this.initCalendar(this.formatDate(this.dateData.curyear, this.dateData.curmonth, 1))
    }
  }
}
</script>

<style lang="scss" scoped>
.container{
  position: relative;
  max-width: 1000px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.15);
  .cal-title{
    font-size: 26px;
    line-height: 26px;
    display: inline-block;
    width: 100%;
    text-align: center;
  }
  .cal-head{
    height: 40px;
    margin: 0 20px;
    display: flex;
    justify-content: space-between;
    align-content: space-around;
    el-button{
      font-size: 26px;
      line-height: 26px;
      flex: 1;
    }
    .cal-head-date{
      line-height: 40px;
    }
  }
  .cal-body{
    margin: 20px 0;
    .cal-date-title{
      display: flex;
      justify-content: space-around;
      align-content: space-around;
      span{
        text-align: center;
      }
    }
    .cal-day{
      display: flex;
      justify-content: space-between;
      align-content: space-around;
      flex-wrap: wrap;
      .day{
        position: relative;
        flex: 0 0 14.285%;
        margin: 10px 0;
        text-align: center;
        color: #999;
      }
      .isCurMonth{
        color: #000;
      }
      i{
        position: absolute;
      }
      .today{
        color: #f29543;
      }
      .selectedday{
        width:30px;
        height: 30px;
        border-radius: 50%;
        border: 1px solid #f29543;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      .hasEvent{
        width:2px;
        height: 2px;
        border-radius: 50%;
        border: 1px solid #f29543;
        background-color: #f29543;
        bottom: -5px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
  .cal-event-container{
    background-color: #f29543;
    border-radius: 10px;
    color: #fff;
    padding: 10px;
    text-align: center;
    h3{
      position: relative;
      display: inline-block;
      width: 60%;
      padding: 6px 20px;
      margin: 10px 0;
      text-align: center;
      font-size: 22px;
      line-height: 22px;
      background-color: rgba(0,0,0,.2);
      border-radius: 20px;
      .add-event{
        position: absolute;
        top: 50%;
        right: 3px;
        transform: translateY(-50%);
      }
    }
    .cal-event{
      border-radius: 5px;
      background-color: #fff;
      text-align: left;
      li{
        color: #000;
        padding: 5px;
        .del-btn{
          color: #f29543;
          float: right;
          font-size: 12px;
          margin-top: 6px;
        }
      }
      .notlast{
        border-bottom: 1px dotted #333;
      }
    }
  }
  .add-dialog-wrap{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(20, 20, 20, 0.5);
    .add-dialog-content{
      background-color: #fff;
      box-shadow: 0 1px 2px 0 rgba(0,0,0,0.15);
      border-radius: 10px;
      padding: 20px;
      max-width: 400px;
      max-height: 90%;
      margin: 20px auto;
    }
  }
}
</style>
<style>
.el-message-box{
  width: 90%;
  max-width: 400px;
}
textarea{
  resize: none !important;
}
</style>



