import Vue from 'vue'
import vuex from 'vuex'

Vue.use(vuex)

const store = new vuex.Store({
  state: {
    eventList: [
      // 事件列表
      {
        start: '2019-04-11',
        end: '2019-04-19',
        title: '待办1',
        desc: ''
      },
      {
        start: '2019-04-19',
        end: '2019-04-21',
        title: '待办2',
        desc: ''
      }
    ]
  },
  mutations: {
    updateEvents (state, eventList) {
      state.eventList = eventList
    }
  }
})

export default store



